@media (min-width: 1024px){
.checkout-fieldgroup__fields-row {
  margin-bottom: 20px;
}

.checkout-fieldgroup__fields-row--checkbox {
  margin-bottom: 26px;
}

.input-text {
  padding-right: 20px;
  padding-left: 20px;
}

.stages__list {
  padding-bottom: 12px;
  border-bottom: 1px solid #f4ede4;
}

.stage-blocks {
  padding: 3px 30px 12px 32px;
}

.multistage-form {
  display: grid;
  grid-template-columns: 1fr 300px;
  grid-template-rows: auto 1fr;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  -moz-box-align: stretch;
  -ms-flex-align: stretch;
  align-items: stretch;
  border-radius: 4px;
  -webkit-filter: drop-shadow(0px 3px 3.5px rgba(0, 0, 0, 0.16));
  filter: drop-shadow(0px 3px 3.5px rgba(0, 0, 0, 0.16));
  background-color: #fffbf6;
}

.multistage-form__summary {
  grid-area: 1 / 2 / 3 / 3;
  background-color: #faf4ed;
}

.multistage-form__stages-list {
  margin-top: 14px;
  margin-bottom: 22px;
}

.multistage-form__buttons {
  display: grid;
  grid-template-columns: auto auto;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 0 30px 30px 32px;
}

.multistage-form__buttons--justify-end {
  -webkit-box-pack: end;
  -webkit-justify-content: end;
  -moz-box-pack: end;
  -ms-flex-pack: end;
  justify-content: end;
}

.woocommerce__multistage-form {
  margin: 32px 0 120px;
}

.woocommerce .woocommerce-checkout-review-order-table {
  padding: 8px 22px 30px;
}

.woocommerce .woocommerce-checkout-review-order-table__title {
  opacity: 1;
}

.express-checkout__label {
  margin-bottom: 20px;
  margin-left: 10px;
  font-family: "Roboto Slab", sans-serif;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.03em;
}

.express-checkout__payment-methods {
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  margin-bottom: 19px;
}

.express-checkout__text-inner {
  text-transform: uppercase;
}

.Product-cartPage .Product_title {
  margin-bottom: 19px;
}

.BtnYellow.BtnYellow-payNow {
  width: 313px;
  max-width: 100%;
}

.BtnYellow.BtnYellow-multistepForm {
  width: 200px;
  max-width: 100%;
  padding-top: 23px;
  padding-bottom: 23px;
  font-size: 15px;
  letter-spacing: 0;
}

.BtnOutline.BtnOutline-multistepForm {
  width: 200px;
  max-width: 100%;
  padding-top: 23px;
  padding-bottom: 23px;
  font-size: 15px;
  letter-spacing: 0;
}

.nf-form-cont {
  padding: 40px 60px 26px;
}
}

@media (min-width: 1200px){
.multistage-form {
  grid-template-columns: 1fr 363px;
}
}

@media (min-width: 768px){
.nf-form-cont .nf-form-title h3 {
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 1.44;
}

.nf-form-cont .nf-form-content .nf-field-container {
  margin-bottom: 16px;
}
}

